<template>
<div>
  <el-form v-model='getRuleForm'>
    <el-row>
      <el-col :span='7'>
        <el-form-item label='车场名称' style='width:260px;'>
              <el-select v-model='getRuleForm.parkingId' style='width:190px;' v-show='true' clearable>
                  <el-option
                  v-for='item in parkingIdOptions'
                  :key='item.id'
                  :label='item.label'
                  :value='item.value'>
                  </el-option>
              </el-select>
           </el-form-item>
      </el-col>
      <el-col :span='6'>
        <el-form-item label='收费规则名称'>
              <el-input v-model='getRuleForm.feeRuleName' style='width:180px;'></el-input>
           </el-form-item>
      </el-col>
      <el-col :span='1'>&nbsp;</el-col>
      <el-col :span='5'>
        <el-form-item label='状态' class='laeblStyle'>
              <el-select v-model='getRuleForm.status' class='handle-input'  clearable>
                  <el-option
                  v-for='item in statusOptions'
                  :key='item.id'
                  :label='item.label'
                  :value='item.value'>
                  </el-option>
              </el-select>
           </el-form-item>
      </el-col>
        <el-col :span='2'>
            <el-button type='button' class='el-button el-button--primary' @click='getCarMsg()'>
                查询
              </el-button>
        </el-col>
    </el-row>
  </el-form>
  <!-- 添加表格 -->
  <el-table
  :data="tableCar"
  style="width: 100%;padding:20px">
  <el-table-column
    prop="id"
    label="id">
  </el-table-column>
  <el-table-column
    prop="parkingName"
    label="车场名称">
  </el-table-column>
  <el-table-column
    prop="feeRuleName"
    label="收费规则名称">
  </el-table-column>
  <el-table-column
    prop="maxFee"
    label="最高收费金额(元)">
  </el-table-column>
  <el-table-column
    prop="status"
    label="状态">
  </el-table-column>
  <!-- 操作 -->
  <el-table-column
  label="操作" width='300'>
  <template slot-scope="scope">
      <el-button
          @click="handleChangeMsg(scope.$index, scope.row)"
          type="primary"
          size="small">查看
      </el-button>
      <el-button
          @click="handleDetailMsg(scope.$index, scope.row)"
          type="primary"
          size="small">编辑
      </el-button>
  </template>
  </el-table-column>
</el-table> 
<!-- 分页插件 -->
<el-pagination
  background
  layout='total,sizes,prev,pager,next,jumper'
  :total='total' v-show='paginationShow'
  style="margin-top:10px;"
  :page-size='pageSize'
  :page-sizes="[5,10,20,30]"
  @size-change="handleSizeChange"
  @current-change='currentChange'
  :current-page='currentPage'>
</el-pagination>
<!-- 详情模态框 -->
<el-dialog title='详情信息' :visible.sync='dialogVisible2' width='1100px'>
  <el-form v-model='detailMsgForm'>
    <div class='form-item-box'>
      <div class='dynamicBox1'>
        <div class='form-box-horizon form-box-1'>
            <el-form-item label='车场名称'>
                <el-input v-model='detailMsgForm.parkingName' style='width:60%' readonly></el-input>
              </el-form-item>
              <el-form-item label='收费规则名称'>
                <el-input v-model='detailMsgForm.feeRuleName' style='width:60%' readonly></el-input>
              </el-form-item>
              <el-form-item label='最高金额 (元)'>
                <el-input v-model='detailMsgForm.maxFee' style='width:50%' readonly></el-input>
              </el-form-item>
              <el-form-item label='状态'>
                <el-input v-model='detailMsgForm.status' style='width:60%' readonly></el-input>
              </el-form-item>
        </div>
        <!-- 内容 -->
        <div v-for='item in detailMsgForm.scArr'>
            <el-row>
                <el-col :span='5'>
                  <el-form-item label='时长开始 (分钟)'>
                    <el-input v-model='item.startMin' class='sjInput'></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span='1'>
                  &nbsp;
                </el-col>
                <el-col :span='5'>
                  <el-form-item label='时长结束 (分钟)'>
                    <el-input v-model='item.endMin' class='sjInput'></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span='1'>
                    &nbsp;
                  </el-col>
                <el-col :span='5'>
                  <el-form-item label='周期类型'>
                    <el-select v-model='item.cycleTypeId'  style='width:120px;'>
                      <el-option 
                      v-for='item in cycleTypeIdOption'
                      :key='item.id'
                      :value='item.value'
                      :label='item.label'>
                      </el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span='1'>
                    &nbsp;
                  </el-col>
                <el-col :span='5'>
                  <el-form-item label='周期金额(元)'>
                    <el-input v-model='item.cycleFee' class='sjInput'></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span='1'>
                    &nbsp;
                  </el-col>
            </el-row>
        </div>
      </div>
    </div>
  </el-form>
</el-dialog>
<!-- 编辑模态框 -->
<el-dialog title='编辑页面' :visible.sync='dialogVisible3' width='1100px'>
    <el-form v-model='changeMsgForm'>
      <div class='form-item-box'>
        <div class='dynamicBox1'>
          <div class='form-box-horizon form-box-1'>
              <el-form-item label='车场名称'>
                  <el-select v-model='changeMsgForm.parkingName' style='width:60%' v-show='true' clearable>
                      <el-option
                      v-for='item in parkingIdOptions'
                      :key='item.id'
                      :label='item.label'
                      :value='item.value'>
                      </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label='收费规则名称'>
                  <el-input v-model='changeMsgForm.feeRuleName' style='width:60%' ></el-input>
                </el-form-item>
                <el-form-item label='最高金额 (元)'>
                  <el-input v-model='changeMsgForm.maxFee' style='width:50%' ></el-input>
                </el-form-item>
                <el-form-item label='状态'>
                  <!-- <el-input v-model='changeMsgForm.status' style='width:60%' ></el-input> -->
                  <el-select v-model='changeMsgForm.status' style='width:50%' clearable>
                     <el-option 
                     v-for='item in statusOptions'
                     :key='item.id'
                     :label='item.label'
                     :value='item.value'>
                     </el-option>
                  </el-select>


                </el-form-item>
          </div>
          <!-- 内容 -->
          <div v-for='item in changeMsgForm.scArr'>
              <el-row>
                  <el-col :span='5'>
                    <el-form-item label='时长开始 (分钟)'>
                      <el-input v-model='item.startMin' class='sjInput'></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span='1'>
                    &nbsp;
                  </el-col>
                  <el-col :span='5'>
                    <el-form-item label='时长结束 (分钟)'>
                      <el-input v-model='item.endMin' class='sjInput'></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span='1'>
                      &nbsp;
                    </el-col>
                  <el-col :span='5'>
                    <el-form-item label='周期类型'>
                      <el-select v-model='item.cycleTypeId'  style='width:120px;'>
                        <el-option 
                        v-for='item in cycleTypeIdOption'
                        :key='item.id'
                        :value='item.value'
                        :label='item.label'>
                        </el-option>
                      </el-select>
                    </el-form-item>
                  </el-col>
                  <el-col :span='1'>
                      &nbsp;
                    </el-col>
                  <el-col :span='5'>
                    <el-form-item label='周期金额(元)'>
                      <el-input v-model='item.cycleFee' class='sjInput'></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span='1'>
                      &nbsp;
                    </el-col>
              </el-row>
          </div>
        </div>
                <!-- 按钮行 -->
                <div style='float:right;margin:20px 5% 0 0;'>
                  <el-button class="el-button el-button--warning" style='margin-right:30px;width:80px' @click='cancelBJ()'>取消</el-button>
                <el-button class="el-button el-button--primary" style='width:80px' @click='handleBJ(changeMsgForm)'>保存</el-button>
              </div>
              <div style="clear:both"></div>



      </div>
    </el-form>
  </el-dialog>
<!-- 新增窗口模态框 -->
<el-dialog title='新增规则' :visible.sync="dialogVisibleAddRule" >
    <el-form v-model='addMsgForm'>
        <div class='form-item-box'>
          <div class='dynamicBox1'>
            <div class='form-box-horizon form-box-1'>
                <el-form-item label='车场名称'>
                    <el-select v-model='addMsgForm.parkingId' class='handle-input' 
                     clearable  style='width:60%' @change='sendParkId(addMsgForm.parkingId)'>
                        <el-option
                        v-for='item in parkingIdOptions'
                        :key='item.id'
                        :label='item.label'
                        :value='item.value'>
                        </el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label='车辆类型'>
                    <el-select v-model='addMsgForm.vehicleType' style='width:60%' clearable>
                        <el-option
                        v-for='item in vehicleTypeOptions'
                        :key='item.id'
                        :label='item.label'
                        :value='item.value'>
                        </el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label='收费规则'>
                    <el-select v-model='addMsgForm.feeRuleId' style='width:60%' clearable >
                        <el-option
                        v-for='item in feeRuleIdOptions'
                        :key='item.id'
                        :label='item.label'
                        :value='item.value'>
                        </el-option>
                    </el-select>
                  </el-form-item>
            </div>
          </div>
          <!-- 按钮行 -->
            <div style='float:right;margin:20px 5% 0 0;'>
                <el-button class="el-button el-button--warning" style='margin-right:30px;width:80px' @click='cancel2()'>取消</el-button>
              <el-button class="el-button el-button--primary" style='width:80px' @click='saveInsertMsg'>保存</el-button>
            </div>
            <div style="clear:both"></div>
        </div>
      </el-form>
</el-dialog>


</div>
</template>
<script>
  import moment from 'moment'
  import qs from 'qs'
  import * as carMsg from '../../api/carMsg'
  export default {
    methods: {
      handleBJ(val){
        console.log(val);
        var self=this;
        var url=self.baseUrl+"feeRule/update";
        // var params={
        //   "id":self.sfgzRow.id,
        //   "parkingId":val.parkingId,
        //   "feeRuleName":val.feeRuleName,
        //   "maxFee":val.maxFee,
        //   "feeRuleConfigs":[
        //     {}
        //   ]
        // }
        val.feeRuleConfigs=val.scArr;
        self.$axios.post(url,val).then(function(res){
          console.log(res.data)
          self.$message({
            message:res.data.message,
            type:"info"
          })
          self.dialogVisible3=false;
          self.getCarMsg();
        }).catch(err=>console.log(err));
      },
      cancelBJ(){
        this.dialogVisible3=false;
      },
      getCycleType(){
        var self=this;
        carMsg.initCycleType().then(res=>{
          self.cycleTypeIdShow=true;
          for(var i=0;i<res.data.data.length;i++){
          self.cycleTypeIdOption[i]={
            id:res.data.data[i].cycleTypeId,
            value:res.data.data[i].cycleTypeId,
            label:res.data.data[i].cycleTypeName,
          }
          }
        }).catch(err=>console.log(err))
      },
      // getCycleType(){
      //   var self=this;
      //   var url=self.baseUrl+"feeRule/initCycleType"
      //   self.$axios.get(url).then(function(res){
      //     self.cycleTypeIdShow=true;
      //     for(var i=0;i<res.data.data.length;i++){
      //     self.cycleTypeIdOption[i]={
      //       id:res.data.data[i].cycleTypeId,
      //       value:res.data.data[i].cycleTypeId,
      //       label:res.data.data[i].cycleTypeName,
      //     }
      //     }
      //     console.log(self.cycleTypeIdOption)
      //   }).catch(err=>console.log(err))
      // },
      saveEditMsg(val){
        var self=this;
        var url=self.baseUrl+"feeRule/updateRuleForVehicleType";
        var params={
          "parkingId":self.changeMsgForm.parkingId,
          "id":self.changeMsgForm.id,
          "vehicleTypeId":self.changeMsgForm.vehicleTypeId,
          "feeRuleId":self.changeMsgForm.feeRuleId,
        }
        self.$axios.post(url,params).then(function(res){
          console.log(res.data)
          self.$message({
            message:res.data.message,
            type:"info"
          })
          self.dialogVisible3=false;
        }).catch(function(err){
          console.log(err);
        })




      },
      cancel2(){
        this.dialogVisibleAddRule=false;
      },
      saveInsertMsg(){
        var self=this;
        var url=self.baseUrl+"feeRule/addRuleForVehicleType";
        var params={
          "parkingId":self.addMsgForm.parkingId,
          "vehicleTypeId":self.addMsgForm.vehicleType,
          "feeRuleId":self.addMsgForm.feeRuleId,
        }
        console.log(params);
        self.$axios.post(url,params).then(function(res){
          console.log(res.data)
          if(res.data.status=='0'){
            self.$message({
              message:res.data.message,
              type:"success"
            })
            self.dialogVisibleAddRule=false;
            self.search();
          }else{
            self.$message({
              message:res.data.message,
              type:"info"
            })
          }
        }).catch(function(err){
          console.log(err);
        })
      },
      addMsg(){
        var self=this;
        self.dialogVisibleAddRule=true;
      },
      //下拉列表事件
      sendParkId(val){
        console.log(val)
        this.ruleType(val);
      },
      // 收费类型
      ruleType(val){
        var self=this;
        self.feeRuleIdOptionsShow=false;
        var url=self.baseUrl+"feeRule/initRuleList/"+val
        self.$axios.get(url).then(res=>{
          console.log('ruleType   res')
          console.log(res.data.data)
          self.feeRuleIdOptionsShow=true;
          for(var i=0;i<res.data.data.length;i++){
            self.feeRuleIdOptions[i]={
              id:res.data.data[i].id,
              label:res.data.data[i].feeRule,
              value:res.data.data[i].id
            }
          }
          console.log(self.feeRuleIdOptions)
        }).catch(err=>{
          console.log(err);
        })
      },
      search(){
        var self=this;
        var params={
          "parkingId":self.getRuleForm.parkingId,
          "vehicleTypeId":self.getRuleForm.vehicleTypeId,
          "feeRuleId":self.getRuleForm.feeRuleId,
          "currentPage":self.currentPage,
          "pageSize":self.pageSize
        };
        self.axiosMsg(params);
      },
      cancel(){
        this.dialogVisible3=false;
      },

      getParkingId(){
        var self=this;  
         carMsg.parkingId(localStorage.getItem("userId")).then(res=>{
              if(res.data.status=='0'){
              for(var i=0;i<res.data.data.length;i++){
                self.parkingIdOptions[i]={
                  id:res.data.data[i].id,
                  label:res.data.data[i].parkingName,
                  value:res.data.data[i].id
                }
              }
            }
            self.$forceUpdate();
         }).catch(err=>{
           console.log(err);
         })
      },
      saveMsg(val){
        var self=this;
        console.log('this.detailForm2.plateColorNo');
        console.log(self.detailForm2.vehicleType);
          var params={
            "id":self.row.id,
            "parkingId":self.detailForm2.parkingId,
            "vehicleTypeId":String(self.detailForm2.vehicleType),
            "plateNo":self.detailForm2.plateNo,
            "plateColorNo":self.detailForm2.plateColorNo,
            "plateColor": self.detailForm2.plateColor,
            "vehicleModel":self.detailForm2.vehicleModel,

            "vehicleOwner":self.detailForm2.vehicleOwner,
            "ownerIdNo":self.detailForm2.ownerIdNo,
            "ownerGender":self.detailForm2.ownerGender,
            "ownerBirth":self.detailForm2.ownerBirth,
            "ownerPhone":self.detailForm2.ownerPhone,
            "ownerAddress":self.detailForm2.ownerAddress,
            "rfidNo":self.detailForm2.rfidNo,
          }
        self.axiosChangeCar(params);
      },
      axiosChangeCar(params){
        var self=this;
        var url=self.baseUrl+"vehicleInfo/updateVehicle";
        self.$axios.post(url,params).then(function(res){
          console.log('res.data.......');
          console.log(res.data);
        }).catch(function(err){
          console.log(err);
        })              
      },
      currentChange(currentPage){
          this.getTime();
          var self=this;
          this.currentPage=currentPage;
            var url='http://58.210.46.54:8000/listGcjlData';
            var paramString={
          "parkingId":self.getRuleForm.parkingId,
          "vehicleTypeId":self.getRuleForm.vehicleTypeId,
          "feeRuleId":self.getRuleForm.feeRuleId,
          "currentPage":self.currentPage,
          "pageSize":self.pageSize
            }
            var jsonParams=JSON.stringify(paramString);
          this.axiosMsg(jsonParams);
      },
      handleSizeChange(val){
          this.pageSize=val;
          var self=this;
          var url='http://58.210.46.54:8000/listGcjlData';
          var str={
            "parkingId":self.getRuleForm.parkingId,
          "vehicleTypeId":self.getRuleForm.vehicleTypeId,
          "feeRuleId":self.getRuleForm.feeRuleId,
          "currentPage":self.currentPage,
          "pageSize":self.pageSize
          }
          var jsonParams=JSON.stringify(str);
           this.axiosMsg(jsonParams);
          console.log(`每页显示${val}条`)
      },
      axiosMsg(params){
        console.log("参数。。")
        console.log(params)
        var self=this;
        carMsg.ruleMsg(params).then(res=>{
          self.paginationShow=true;
          self.total=res.data.data.total;
          self.tableCar=res.data.data.records;
          for(var i=0;i<self.tableCar.length;i++){
            !self.tableCar[i].maxFee?self.tableCar[i].maxFee='0':self.tableCar[i].maxFee=Math.ceil(self.tableCar[i].maxFee/100);
            self.tableCar[i].status=='1'?self.tableCar[i].status='正常':self.tableCar[i].status='停用';
          }
        }).catch(function(err){
          console.log(err);
        })
      },
      momentTime(val){
          return moment(val).format("YYYY-MM-DD HH:mm:ss");
      },
      momentTime1(val){
          return moment(val).format("YYYY-MM-DD");
      },
      getCarMsg(){
        var self=this;
          //查询，获取参数
          var str={
            'parkingId':self.getRuleForm.parkingId,
            'feeRuleName':self.getRuleForm.feeRuleName,
            "status":self.getRuleForm.status,
            "pageSize":this.getRuleForm.vehicleType,
          }
          var jsonParams=JSON.stringify(str);
          this.axiosMsg(jsonParams);
      },
      handleDetailMsg(index,row,scope){
        var self=this;
        this.dialogVisible3=true;
        self.sfgzRow=row;
        console.log('row.....')
        console.log(row)
        //调用回显;
        var url=self.baseUrl+"feeRule/detail?id="+row.id;
        self.$axios.get(url).then(function(res){
          self.changeMsgForm=res.data.data;
          self.changeMsgForm.scArr=[];
          for(var i=0;i<res.data.data.feeRuleConfigs.length;i++){
            self.changeMsgForm.scArr[i]={
              "startMin":res.data.data.feeRuleConfigs[i].startMin,
              "endMin":res.data.data.feeRuleConfigs[i].endMin,
              "cycleTypeId":res.data.data.feeRuleConfigs[i].cycleTypeId,
              "cycleFee":res.data.data.feeRuleConfigs[i].cycleFee,
            }
          }
        })
      },
      handleChangeMsg(index,row,scope){
        console.log("row====")
        console.log(row)
        var self=this;
        self.dialogVisible2=true;
        var url=self.baseUrl+"feeRule/detail?id="+row.id;
        self.$axios.get(url).then(function(res){
          self.detailMsgForm=res.data.data;
          self.detailMsgForm.scArr=[];
          console.log('detailMsgForm.status.....')
          if(self.detailMsgForm.status=='0'){
            console.log(1111)
            self.detailMsgForm.status='停用'
          }else{
            console.log(2222)
            self.detailMsgForm.status='正常'
          }
          for(var i=0;i<res.data.data.feeRuleConfigs.length;i++){
            self.detailMsgForm.scArr[i]={
              "startMin":res.data.data.feeRuleConfigs[i].startMin,
              "endMin":res.data.data.feeRuleConfigs[i].endMin,
              "cycleTypeId":res.data.data.feeRuleConfigs[i].cycleTypeId,
              "cycleFee":res.data.data.feeRuleConfigs[i].cycleFee,
            }
          }
        })
      }
    },
    data() {
      return {
        statusOptions:[
          {id:0,value:0,label:"停用"},
          {id:1,value:1,label:"正常"},
        ],
        sfgzRow:[],
        cycleTypeIdOption:[],
        scArrShow:false,
        statusOptions:[
          {id:1,value:1,label:"正常"},
          {id:0,value:0,label:"停用"}
        ],
        getRuleForm:{},
        parkingIdOptions:[],
        parkingIdOptionsShow:false,
        feeRuleIdOptionsShow:false,
        feeRuleIdOptions:[],
        vehicleTypeOptions:[],
        vehicleTypeOptionsShow:false,
        currentPage:1,
        total:0,
        tableCar:[],
        paginationShow:false,
        pageSize:10,
        dialogVisible2:false,
        detailMsgForm:{
          scArr:[]
        },
        changeMsgForm:{
          scArr:[]
        },
        dialogVisible3:false,
        addMsgForm:{},
        dialogVisibleAddRule:false,
        rowMsg:{}
      }
    },
    mounted:function(){
        this.getParkingId();
        this.getCycleType();
        this.getCarMsg();
    }
  }
</script>
<style scoped>
   .sjInput{
    width:100px;
  }
    .plot{
        margin-bottom: 20px;
        width:100%;
    }
    ._ul{
        list-style: none;
    }
    ._li{
        cursor: pointer;
        margin-left: 20px;
        font-size: 14px;
        color:#606266;
    }

    .export_btn{
        color:#606266;
    }
    .export_button:hover .export_btn{
        color: #409eff;
    }
    .form-box-horizon {
        display: flex;
        align-items: center;
    }

    .form-box-horizon .el-form-item {
